
import { defineComponent, ref, Ref, onUnmounted, PropType, onMounted } from 'vue'
import { Division } from '@/utils/generateDivision'

export interface FinishPayload {
  operation: Division
  answer: number
  success: boolean
  score: number
  timestamp: number
}

export default defineComponent({
  name: 'Started',
  props: {
    operation: { type: Object as PropType<Division>, required: true }
  },
  emits: ['finished'],
  setup: (props, { emit }) => {
    const start = Date.now()
    const answer: Ref<HTMLInputElement | null> = ref(null)

    const result = ref('')
    const elapsed = ref(0)

    const timer = setInterval(() => {
      elapsed.value++
    }, 1000)

    const checkResult = () => {
      const success: boolean = Number(result.value) === props.operation.result
      const timestamp = Date.now()
      emit('finished', {
        operation: { ...props.operation },
        answer: Number(result.value),
        success,
        score: Math.round((Date.now() - start) / 1000) + (success ? 0 : 60),
        timestamp
      })
    }

    onMounted(() => {
      // eslint-disable-next-line no-unused-expressions
      answer.value?.focus()
    })

    onUnmounted(() => {
      clearInterval(timer)
    })

    return { elapsed, result, checkResult, answer }
  }

})
